.slick-arrow {
    position: absolute;
    bottom: 0;
    margin: 0;
    padding: 0;
    line-height: 0;
    font-size: 0;

    width: 76px;
    height: 76px;
    border-radius: 25px;
    box-shadow: 0 25px 18px -2px #151843;
    background-image: $blue-gradient;
    fill: #e3873a;
    transition: fill .3s ease-in-out;
    .no-touch &:after {
        content: '';
        opacity: 0;
        border-radius: inherit;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: $light-orange;
        transition: opacity .3s ease-in-out;
        z-index: -1;
    }
    .no-touch &:hover {
        fill: $white;
        &:after {
            opacity: 1;
        }
    }
    &-icon {
        fill: inherit;
        width: 22px;
        height: 16px;
    }
    &.slick-prev &-icon {
        transform: scaleX(-1);
    }
    &.slick-prev {
        left: 50%;
        transform: translateX(-100%);
        margin-left: -10px;
    }
    &.slick-next {
        right: 50%;
        transform: translateX(100%);
        margin-right: -10px;
    }
    @include media(">=tablet") {
        top: 50%;
        &.slick-next {
            margin: 0;
            right: 0;
            transform: translate(0, -50%);
        }
        &.slick-prev {
            margin: 0;
            left: 0;
            transform: translate(0, -50%);
        }
    }
}