.button,
input[type="submit"],
button {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 52px;
    margin: 0 0 10px;
    padding: 5px 30px;
    color: $white;
    background-color: $orange;
    border: 1px solid transparent;
    border-radius: 35px;
    line-height: 30px;
    font-size: 16px;
    font-weight: 700;
    text-align: center;
    text-decoration: none;
    letter-spacing: .6px;
    white-space: nowrap;
    cursor: pointer;
    -webkit-appearance: none;
    transition: background-color .3s ease-in-out;
    @include media(">=tablet") {
        height: 72px;
    }
    .no-touch  &:hover,
    .no-touch &:focus {
        color: $white;
        background-color: $light-orange;
    }
    &.decor {
        padding-right: 60px;
        position: relative;
        z-index: 0;
        justify-content: flex-start;
        @include media(">=tablet") {
            padding-right: 80px;
        }
        &:before,
        &:after {
            content: '';
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            z-index: -1;
        }
        &:after {
            right: 15px;
            width: 22px;
            height: 16px;
            background-image: url(../images/icons/arrow-right-white.svg);
            background-size: 100% 100%;
            @include media(">=tablet") {
                right: 25px;
            }
        }
        &:before {
            right: 0;
            width: 52px;
            height: 52px;
            border-radius: inherit;
            background-color: $light-orange;
            transition: background-color .3s ease-in-out, width .3s ease-in-out;
            @include media(">=tablet") {
                height: 72px;
                width: 72px;
            }
        }
    }
}