// forms

input[type="text"],
input[type="password"],
input[type="email"],
input[type="tel"],
input[type="number"] {
    display: inline-block;
    width: 100%;
    height: 52px;
    margin: 0 0 10px;
    padding: 5px 33px;
    background: $blue;
    color: $white;
    border: 1px solid transparent;
    font-size: 12px;
    line-height: 1.31;
    font-weight: 700;
    letter-spacing: .6px;
    vertical-align: middle;
    outline: none;
    -webkit-appearance: none;
    -webkit-border-radius: 0;
    @include media(">=tablet") {
        height: 72px;
        font-size: 16px;
    }
    @include placeholder {
        color: rgba($white, .6);
    }
    &:focus {
        color: $white;
    }
}

select {
    @include clearSelect;
    display: inline-block;
    width: 100%;
    height: 52px;
    margin: 0 0 10px;
    padding: 5px 23px;

    background: $blue;
    color: rgba($white, .6);
    border: 1px solid transparent;

    font-size: 12px;
    line-height: 1.31;
    font-weight: 700;

    vertical-align: middle;
    outline: none;
    @include media(">=tablet") {
        height: 72px;
        font-size: 16px;
        padding-left: 33px;
        padding-right: 33px;
    }

    &:focus {
        color: $white;
    }
}

input[type="checkbox"] {
    display: inline-block;
    margin: 0 0 $main-margin;
    vertical-align: middle;
}

input[type="radio"] {
    display: inline-block;
    margin: 0 0 $main-margin;
    vertical-align: middle;
}

label {
    display: inline-block;
    margin: 0 0 $main-margin;
    vertical-align: middle;
}

body {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    justify-content: center;
}