.subscribe {
    padding: 51px 15px 40px;
    margin-bottom: 57px;
    border-radius: 10px;
    background-image: $block-gradient;
    text-align: center;
    @include media(">=tablet-landscape") {
        @include flex(row);
        padding: 0;
        margin-bottom: 100px;
        overflow: hidden;
        text-align: left;
        background-image: none;
    }
    @include media(">=medium-desktop") {
        margin-bottom: 40px;
    }

    .subscribe-frame {
        width: 100%;
        @include media(">=tablet") {
            width: 1920px;
        }
    }
}

.subscribe-title {
    position: relative;
    z-index: 5;
    h2 {
        @extend .h1;
        margin-bottom: 31px;
    }
    @include media(">=tablet-landscape") {
        width: calc(100% - 556px);
        padding: 70px 30px;
        background-image: $block-gradient;
    }
    @include media(">=desktop") {
        padding-left: 68px;
    }
    @include media(">=medium-desktop") {
        width: 52%;
        padding-left: 110px;
        padding-bottom: 88px;
    }
}

.subscribe-form {
    @include media(">=tablet-landscape") {
        width: 556px;
        padding: 69px 38px 70px;
        background-image: linear-gradient(180deg, #141b47 0%, rgba(17, 24, 69, 0) 100%);
    }
    @include media(">=medium-desktop") {
        width: 48%;
        padding: 79px 65px 88px 38px;
    }
}

.subscribe-form-inner {
    > strong {
        display: block;
        margin-bottom: 20px;
        font-size: 14px;
    }
    @include media(">=tablet-landscape") {
        max-width: 480px;
        margin: 0 auto;
        > strong {
            font-size: 24px;
            margin-bottom: 15px;
        }
    }
}

.subscribe-frame {
    max-width: 767px;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    @include media(">=tablet") {
        max-width: none;
        width: 1920px;
    }
}