body {
    &.is-locked {
        @include media("<desktop") {
            //position: fixed;
            //left: 0;
            //top: 0;
            width: 100%;
            overflow: hidden;
            height: 100%;
        }
    }
    &.with-hero .main {
        padding-top: 0;
    }
}

.main {
    padding-top: 48px;
    flex-shrink: 0;
    flex-grow: 1;
    // overflow: hidden;
    @include media(">=desktop") {
        padding-top: 153px;
    }
    @include media(">=large-desktop") {
        padding-top: 163px;
    }
}

.wrapper {
    position: relative;
    overflow: hidden;
    width: 100%;
    @include flex(column);
    flex-shrink: 0;
    flex-grow: 1;
    min-height: 100vh;
    height: 100%;
}

.container {
    max-width: 1920px;
    margin: 0 auto;
    padding: 0 20px;
}

.container {
[class*="grid-"] {

        @include media(">=desktop") {
            margin: 0 -20px;
        }
    }
}

.header,
.footer {
    flex-shrink: 0;
}

.tabs-content {
    &:not(.is-active) {
        position: absolute;
        visibility: hidden;
        opacity: 0;
        z-index: -1;
        transition: all 0s ease-in-out;
    }
    &.is-active {
        transition: all .3s ease-in-out;
    }
}

.image-decoration-bg {
    position: relative;
    z-index: 5;
    @include media(">=tablet-landscape") {
        &:after {
            content: '';
            position: absolute;
            top: 0;
            left: 50%;
            transform: translateX(-50%);
            min-width: 2200px;
            width: 100%;
            height: 100%;
            background-image: url(../images/decor-bg-2.png);
            background-size: 100% 100%;
            background-position: center center;
            z-index: -1;
            pointer-events: none;
        }
        &-alternative:after {
            top: -334px;
            height: auto;
            bottom: 200px;
            background-image: url(../images/decor-bg-3.png);
        }
    }
}

.pre-decor-section {
    position: relative;
    z-index: 10;
}

.decor-section {
    position: relative;
    margin-top: -202px;
    padding-top: 254px;
    background-image: linear-gradient(180deg, #121946 0%, $dark-blue 100%);
    z-index: 0;
    @include media(">=tablet") {
        margin-top: -413px;
        padding-top: 411px;
    }
    &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100px;
        background: $dark-blue;
        z-index: -1;
    }
    &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
        width: 220%;
        height: 200px;
        background: $dark-blue;
        border-radius: 0 0 100% 0;
        z-index: -1;
        @include media(">=tablet") {
            transform: translateX(-50%) rotate(-2deg);
            height: 500px;
        }
        @include media(">=medium-desktop") {
            width: 120%;
        }
    }
}

// Static content

.static-content {
    text-align: center;
    max-width: 640px;
    margin: 0 auto;
    padding-bottom: 70px;
    @include media(">=tablet-landscape") {
        padding-bottom: 118px;
    }
    & > *:last-child {
        margin-bottom: 0;
    }
    > strong {
        display: block;
        margin-bottom: 3px;
        color: rgba($light-orange, .9);
        font-size: 12px;
        font-weight: 700;
        text-transform: uppercase;
        letter-spacing: 0.15px;
        @include media(">=tablet") {
            font-size: 14px;
        }
        @include media(">=medium-desktop") {
            .dual-box.decor-box & {
                margin-bottom: 0;
            }
        }
    }
    h2 {
        @extend .h1;
        @include media(">=desktop") {
            margin-bottom: 40px;
        }
    }
    p {
        margin-bottom: 45px;
        color: $gray;
        font-weight: 500;
        line-height: 1.7;
        letter-spacing: 0.2px;
    }
    .button {
        min-width: 100%;
        margin-left: auto;
        margin-right: auto;
        @include media(">=tablet") {
            min-width: 231px;
            width: 0;
        }
    }
}