.story-tip {
    border-radius: 10px;
}

.story-tip-content {
    padding: 49px 34px 46px;
    text-align: center;
    background-image: linear-gradient(180deg, #111845 70%, $dark-blue 100%);
    border-radius: 10px 10px 0 0;
    & > *:last-child {
        margin-bottom: 0;
    }
    h3 {
        margin-bottom: 21px;
    }
    @include media(">=tablet-landscape") {
        padding: 71px 34px 66px;
        h3 {
            margin-bottom: 15px;
        }
    }
    @include media(">=medium-desktop") {
        padding: 68px 62px 69px 67px;
        text-align: left;
        h3 {
            margin-bottom: 23px;
        }
    }
}

.story-tip-link {
    display: block;
    position: relative;
    padding: 21px 45px 23px 30px;
    border-radius: 0 0 10px 10px;
    color: $white;
    fill: $white;
    background-image: $blue-gradient;
    box-shadow: 0 24px 18px -5px #151843;
    font-weight: 700;
    overflow: hidden;
    z-index: 0;
    &:after {
        content: '';
        opacity: 0;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: $light-orange;
        transition: inherit;
        z-index: -1;
    }
    .no-touch &:hover {
        box-shadow: 0 24px 18px -5px transparent;
        &:after {
            opacity: 1;
        }
    }
    .arrow-icon {
        width: 22px;
        height: 16px;
        position: absolute;
        right: 15px;
        top: 50%;
        transform: translateY(-50%);
    }
    @include media(">=tablet-landscape") {
        padding: 23px 65px 22px 36px;
        .arrow-icon {
            right: 39px;
        }
    }
    @include media(">=medium-desktop") {
        padding-left: 66px;
    }
}