.hero {
    min-height: 100vh;
    padding: 97px 0 128px;
    position: relative;
    background-size: cover;
    background-position: center bottom;
    text-align: center;
    z-index: 0;
    &:after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 40%;
        background-image: linear-gradient(180deg, rgba(4, 9, 41, 0) 0%, $dark-blue 100%);
        z-index: -1;
    }
    @include media(">=desktop") {
        padding: 220px 0 40px;
        min-height: 0;
        height: 100vh;
        &:after {
            height: 20%;
        }
    }
    @include media(">=large-desktop") {
        padding-top: 241px;
    }
}
