.hero-inner {
    padding-bottom: 29px;
    & > *:last-child {
        margin-bottom: 0;
    }
    h1 {
        margin-bottom: 11px;
    }
    p {
        max-width: 410px;
        margin: 0 auto 15px;
        a {
            color: $orange;
            .no-touch &:hover {
                color: $white;
            }
        }
    }

}