.steps {
    margin-bottom: 0;
    li {
        position: relative;
        border-radius: 68px;
        padding: 0 16px;
        margin-bottom: -13px;
        color: $gray;
        transition: all .3s ease-in-out;
        z-index: 0;
        @include media(">=medium-desktop") {
            margin-bottom: -21px;
        }
        &:before {
            counter-increment: counterName;
            content:  "0" counters(counterName, "-");
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            left: 16px;
            color: #0d2152;
            text-shadow: 0 21px 18px #151843;
            font-size: 24px;
            font-weight: 700;
            letter-spacing: 0.3px;
            transition: color .3s ease-in-out;
            z-index: -1;
            @include media(">=desktop") {
                left: 20px;
            }
            @include media(">=medium-desktop") {
                font-size: 44px;
                left: 44px;
            }
        }
        .no-touch &:hover {
            background-image: $blue-gradient;
            box-shadow: 0 21px 18px -10px #0d102e;
            &:before {
                color: $orange;
            }
            .arrow-icon {
                fill: $orange;
            }
        }
    }
    a {
        color: inherit;
        display: block;
        padding: 26px 25px 27px 49px;
        font-size: 14px;
        font-weight: 500;
        line-height: (22/14);
        @include media(">=desktop") {
            padding-right: 70px;
        }
        @include media(">=medium-desktop") {
            padding: 35px 52px 36px 113px;
            font-size: 18px;
        }
    }
    .arrow-icon {
        position: absolute;
        top: 50%;
        right: 15px;
        transform: translateY(-50%);
        width: 18px;
        height: 13px;
        fill: $white;
        transition: fill .3s ease-in-out;
        @include media(">=desktop") {
            right: 34px;
        }
        @include media(">=medium-desktop") {
            width: 33px;
            height: 23px;
        }
    }
}