.cookie {
    display: none;
    &.is-accepted {
        display: none;
    }
    @include media(">=tablet-landscape") {
        display: flex;
        position: fixed;
        bottom: 66px;
        left: 50%;
        transform: translateX(-50%);
        width: 76%;
        max-width: 1450px;
        border-radius: 25px;
        box-shadow: 0 21px 18px -10px #0c0d21;
        background-image: linear-gradient(180deg, #0c255b 0%, #0e1d49 100%);
        overflow: hidden;
        opacity: 0;
        transition: opacity .3s ease-in-out;
        z-index: 40;
        &.is-loaded {
            opacity: 1;
        }
    }
}

.cookie-content {
    @include media(">=tablet-landscape") {
        @include flex(column, center, flex-start);
        width: calc(100% - 161px);
        padding: 20px 62px 25px 40px;
        > *:last-child {
            margin-bottom: 0;
        }
        strong {
            display: block;
            margin-bottom: 8px;
            font-size: 12px;
            letter-spacing: 0.15px;
            color: rgba($orange, .9);
            text-transform: uppercase;
        }
        p {
            color: #e8e8ec;
            font-size: 16px;
            font-weight: 500;
            line-height: (24/16);
            letter-spacing: 0.2px;
            a {
                color: inherit;
                text-decoration: underline;
                .no-touch &:hover {
                    text-decoration: none;
                }
            }
        }
    }
}

.cookie-accept {
    @include media(">=tablet-landscape") {
        width: 161px;
    }
}

.cookie-btn {
    @include media(">=tablet-landscape") {
        @include flex(column, center, center);
        height: 100%;
        font-size: 16px;
        font-weight: 700;
        background-color: rgba(255, 255, 255, 0.06);
        letter-spacing: 0.6px;
        color: $orange;
    }
}