.logos {
    padding-top: 13px;
    padding-bottom: 83px;
    @include media(">=tablet-landscape") {
        padding-bottom: 136px;
    }
}

.logos-inner {
    @include flex(row, space-between);
    overflow-x: auto;
    overflow-y: hidden;
    margin: 0 -15px;
    @include media(">=desktop") {
        margin: 0 -27px;
        overflow: visible;
    }
}

.logos-item {
    @include flex(column, center, flex-start);
    font-size: 0;
    line-height: 0;
    height: 24px;
    padding: 0 15px;
    @include media(">=tablet") {
        height: 44px;
    }
    @include media(">=desktop") {
        padding: 0 27px;
    }
    @include media(">=large-desktop") {
        height: 62px;
    }
    img {
        max-height: 100%;
        width: auto;
        max-width: none;
        @include media(">=tablet") {
            max-width: 100%;
        }
    }
}