* {
    outline: none;
}

body {
    font: #{$main-fz}/#{$main-lh} $main-ff;
    letter-spacing: .2px;
    color: $main-text-color;
    background: $dark-blue;
    -webkit-font-smoothing: antialiased;
}

h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
    margin-bottom: 20px;
    line-height: $headings-lh;
    font-weight: 500;
}

h1, .h1 {
    font-size: 28px;
    line-height: (36 / 28);
    letter-spacing: .15px;
    font-weight: 700;
    @include media(">=tablet") {
        font-size: 48px;
        line-height: (63 / 48);
        letter-spacing: .25px;
    }
    @include media(">=large-desktop") {
        font-size: 58px;
        line-height: $headings-lh;
        letter-spacing: .3px;
    }
}

h2, .h2 {
    font-size: 16px;
    line-height: (25 / 16);
    font-weight: 700;
    @include media(">=tablet") {
        font-size: 24px;
        line-height: (34 / 24);
    }
}

h3, .h3 {
    font-weight: 700;
    font-size: 16px;
    @include media(">=tablet") {
        font-size: 24px;
    }
}

h4, .h4 {
    font-size: 15px;
    @include media(">=tablet") {
        font-size: 20px;
    }
}

h5, .h5 {
    font-size: 14px;
    @include media(">=tablet") {
        font-size: 18px;
    }
}

h6, .h6 {
    font-size: 14px;
    @include media(">=tablet") {
        font-size: 16px;
    }
}

p {
    margin-bottom: 20px;
}

a {
    text-decoration: none;
    color: $gray-darken;
    transition: all .3s ease-in-out;
    &:hover {
        color: $white;
    }
}

%ul,
ul {
    margin-bottom: $main-margin;
    li {
        padding: 0 0 12px 20px;
        &:before {
            content: "";
            float: left;
            margin: 10px 0 0 -20px;
            border-radius: 50%;
            width: 5px;
            height: 5px;
            background: $orange;
        }
    }
}

ol {
    margin-bottom: $main-margin;
    counter-reset: counterName;
    li {
        &:before {
            counter-increment: counterName;
            content: counters(counterName, "-") ". ";
        }
    }
    ul,
    ol {
        margin: 0;
        padding-left: 20px;
    }
}

blockquote {
    position: relative;
    margin: 0 0 40px;
    padding-top: 32px;
    color: $gray;
    font-size: 16px;
    line-height: (27 / 16);
    font-weight: 500;
    font-style: italic;
    &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 23px;
        height: 23px;
        border-radius: 4px;
        border: 1px solid #042153;
        background-color: #001841;
        background-image: url(../images/icons/quote.svg);
        background-position: center;
        background-size: 15px 11px;
        background-repeat: no-repeat;
    }
    @include media(">=tablet") {
        min-height: 60px;
        font-size: 18px;
        line-height: (30 / 18);
        padding: 0 0 0 74px;
        &:before {
            width: 58px;
            height: 58px;
            background-size: 35px 23px;
        }
    }
    p {
        margin: 0;
    }
    q {
        quotes: none;
        &:before,
        &:after {
            quotes: none;
        }
    }
    cite {
        display: block;
    }
}

table {
    margin: 0 0 $main-margin;
    th,
    td {
        border: 1px solid $white;
        padding: 5px;
    }
}

img {
    max-width: 100%;
}

.link-arrowed {
    display: inline-block;
    position: relative;
    padding-right: 23px;
    font-weight: 500;
    letter-spacing: 0.2px;
    .no-touch &:hover {
        color: $orange;
    }
    &:after {
        content: '';
        position: absolute;
        right: 0;
        top: 6px;
        width: 17px;
        height: 11px;
        background-image: url(../images/icons/arrow-right-orange.svg);
        background-size: 100% 100%;
    }
    @include media(">=desktop") {
        padding-right: 41px;
        &:after {
            width: 22px;
            height: 16px;
            top: 5px;
        }
    }

}