// BENEFITS SECTION

.benefits {
    margin-bottom: 20px;
    @include media(">=tablet-landscape") {
        margin-bottom: 100px;
    }
    h2 {
        @extend .h1;
        margin-bottom: 56px;
        text-align: center;
        @include media(">=tablet-landscape") {
            margin-bottom: 76px;
        }
        @include media(">=medium-desktop") {
            margin-bottom: 68px;
        }
    }
}

.benefits-heading {
    @include flex(row, center);
    position: relative;
    margin: 0 -5px 20px;
    padding-bottom: 1px;
    @include media(">=tablet") {
        margin: 0 -10px 20px;
    }
    @include media(">=tablet-landscape") {
        margin: 0 -22px 42px;
    }
    @include media(">=medium-desktop") {
        margin-bottom: 55px;
    }
    &:after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 1px;
        background: url(../images/line.png);
        background-size: 100% 100%;
    }
    li {
        @extend %reset-list-bullets;
        text-align: center;
        padding: 0 5px;
        @include media(">=tablet") {
            padding: 0 10px;
        }
        @include media(">=tablet-landscape") {
            padding: 0 22px;
        }
    }
    a {
        display: block;
        position: relative;
        height: 100%;
        padding-bottom: 21px;
        color: #374f78;
        font-size: 14px;
        line-height: (19/14);
        font-weight: 700;
        letter-spacing: 0.18px;
        @include media(">=tablet-landscape") {
            font-size: 24px;
        }
        @include media(">=medium-desktop") {
            padding-bottom: 25px;

        }
        &:after {
            content: '';
            opacity: 0;
            visibility: hidden;
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
            height: 6px;
            border-radius: 8px;
            background-color: $orange;
            transition: all .3s ease-in-out;
            @include media(">=tablet-landscape") {
                left: 6px;
                right: 6px;
            }
        }
        .no-touch &:hover {
            color: $white;
        }
        &.is-active {
            color: $white;
            &:after {
                visibility: visible;
                opacity: 1;
            }
        }
    }
}

.benefits-inner {
    padding-bottom: 36px;
    text-align: center;
    @include media(">=tablet-landscape") {
        padding-bottom: 67px;
        max-width: 640px;
        margin: 0 auto;
    }
    p {
        font-weight: 500;
    }
    & > *:last-child {
        margin-bottom: 0;
    }
}

.information {
    @include media(">=tablet-landscape") {
        @include flex(row, flex-start, center);
        margin: 0 -15px;
    }
    @include media(">=medium-desktop") {
        align-items: flex-start;
    }
}

.information-steps {
    padding-bottom: 45px;
    @include media(">=tablet-landscape") {
        width: 41%;
    }
    @include media(">=medium-desktop") {
        width: 51%;
    }
}

.information-preview {
    @include media(">=tablet-landscape") {
        width: 59%;
    }
    @include media(">=medium-desktop") {
        width: 49%;
    }
}

.information-steps,
.information-preview {
    @include media(">=tablet-landscape") {
        padding: 0 15px;
    }
}

.information-pic {
    font-size: 0;
    line-height: 0;
    @include media(">=tablet", "<tablet-landscape") {
        max-width: 60%;
        margin: 0 auto;
    }
    @include media(">=medium-desktop") {
        text-align: right;
    }
}