// REPORT CARD SECTION

.report-card {
    margin-bottom: 91px;
    border-radius: 10px;
    overflow: hidden;
    position: relative;
    @include media(">=tablet-landscape") {
        margin-bottom: 138px;
        padding-left: 40%;
    }
}

.report-card-content {
    padding: 90px 26px 58px;
    background-image: $block-gradient;
    @include media(">=tablet-landscape") {
        padding: 100px 70px 69px 57px;
    }
    @include media(">=medium-desktop") {
        padding: 100px 93px 153px 119px;
    }

}

.report-card-inner {
    & > *:last-child {
        margin-bottom: 0;
    }
    h3 {
        margin-bottom: 43px;
        line-height: (25/16);
        @include media(">=tablet-landscape") {
            margin-bottom: 57px;
        }
        @include media(">=medium-desktop") {
            margin-bottom: 30px;
        }
    }
}

.report-card-pic {
    display: block;
    line-height: 0;
    font-size: 0;
    margin-bottom: 12px;
    height: 14px;
    @include media(">=tablet-landscape") {
        margin-bottom: 25px;
        height: 31px;
    }
    @include media(">=medium-desktop") {
        height: 17px;
    }
    img {
        max-height: 100%;
    }
}

.report-card-preview {
    position: relative;
    background-size: cover;
    max-height: 280px;
    background-position: center;
    z-index: 0;
    @include media(">=tablet-landscape") {
        position: absolute;
        top: 0;
        left: 0;
        width: 40%;
        height: 100%;
        max-height: none;
    }
    &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-image: linear-gradient(180deg, rgba(6, 12, 48, 0) 60%, $dark-blue 100%);
        z-index: -1;
        //@include media(">=tablet-landscape") {
        //    display: none;
        //}
    }
    &:before {
        content: '';
        display: block;
        height: 0;
        padding-bottom: 101%;
        @include media(">=tablet-landscape") {
            display: none;
        }
    }
    .button {
        position: absolute;
        bottom: 60px;
        left: 50%;
        transform: translateX(-50%);
        min-width: calc(100% - 50px);
        margin: 0;
        @include media("<tablet") {
            max-width: 231px;
            padding: 5px 10px;
            justify-content: center;
            &:after,
            &:before {
                display: none;
            }
        }
        @include media(">=tablet") {
            bottom: 50%;
            transform: translate(-50%, 50%);
            padding-left: 23px;
            width: auto;
            min-width: 297px;
            &:after {
                right: 22px;
                width: 27px;
                height: 33px;
                background-image: url(../images/icons/file.svg);
            }
        }
        @include media(">=tablet-landscape") {
            transform: translateX(-50%);
            bottom: 80px;
        }
    }
}