.reviews-rating {
    position: relative;
    padding-bottom: 76px;
    @include media(">=tablet") {
        padding: 0 37px;
    }
}

.reviews-rating-slider {
    padding: 45px 14px 53px;
    border-radius: 10px;
    background-image: $block-bg-gradient;
    text-align: center;
    transition: opacity .3s ease-in-out;
    &:not(.is-initialized) {
        max-height: 400px;
        visibility: hidden;
        opacity: 0;
        overflow: hidden;
    }
    &.slick-slider {
        position: static;
    }
    .slick-slide {
        padding: 0 20px;
    }
    @include media(">=tablet") {
        padding-left: 50px;
        padding-right: 50px;
    }
    @include media(">=tablet-landscape") {
        padding: 68px 39px 40px 55px;
        text-align: left;
    }
    @include media(">=medium-desktop") {
        padding-top: 71px;
        padding-left: 48px;
    }
}

.rating-content {
    padding-bottom: 5px;
    @include media(">=tablet") {
        padding-bottom: 20px;
    }
    @include media(">=tablet-landscape") {
        padding: 0 10px 64px 0;
    }
    @include media(">=medium-desktop") {
        padding-bottom: 36px;
    }
    & > *:last-child {
        margin-bottom: 0;
    }
    h3 {
        margin-bottom: 15px;
    }
    > strong {
        display: block;
        margin-bottom: 26px;
        color: $light-orange;
        font-size: 12px;
        line-height: (17/12);
        text-transform: uppercase;
        letter-spacing: 0.15px;
        @include media(">=tablet-landscape") {
            font-size: 14px;
        }
    }
    p {
        color: $gray;
        font-weight: 500;
        @include media(">=medium-desktop") {
            max-width: 90%;
        }
    }
}

.rating-actions {
    @include flex(column, center, center);
    @include media(">=tablet") {
        flex-direction: row-reverse;
        justify-content: space-between;
    }
    @include media(">=medium-desktop") {
        max-width: 90%;
    }
}

.rating-info {
    @include flex(row, flex-start, center);
    padding-bottom: 32px;
    @include media(">=tablet") {
        padding-bottom: 0;
    }
    &-text {
        padding-right: 4px;
        color: $gray;
        font-weight: 500;
        font-size: 16px;
        span {
            color: #e88939;
        }
    }
    &-img {
        position: relative;
        width: 38px;
        height: 32px;
        &:after {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            height: 100%;
            width: 100%;
            background-image: url(../images/rating-default.png);
            background-size: 100% 100%;
        }
        span {
            position: absolute;
            top: 0;
            left: 0;
            height: 100%;
            background-image: url(../images/rating-active.png);
            background-size: auto 100%;
            background-repeat: no-repeat;
            z-index: 3;
        }
    }
}

.rating-link {
    display: inline-block;
    position: relative;
    padding-right: 29px;
    color: $white;
    font-weight: 700;
    font-size: 14px;
    text-transform: uppercase;
    vertical-align: top;
    @include media(">=tablet-landscape") {
        padding-right: 22px;
    }
    &:before {
        content: '';
        position: absolute;
        width: 22px;
        height: 22px;
        right: 0;
        top: 50%;
        margin-top: -2px;
        transform: translateY(-50%);
        background-image: url(../images/icons/review-top.svg);
        background-size: 100% 100%;
    }
    .no-touch &:hover {
        opacity: .7;
    }
}