.reviews-section {
    padding-bottom: 40px;
    @include media(">=tablet-landscape") {
        padding-bottom: 0;
    }
    h2 {
        @extend .h1;
        margin-bottom: 43px;
        text-align: center;
        width: 100%;
        @include media(">=tablet-landscape") {
            max-width: 751px;
            margin: 0 auto 72px;
        }
        @include media(">=large-desktop") {
            max-width: 923px;
        }
    }
    @include media(">=tablet-landscape") {
        @include flex(row, flex-start, flex-start, wrap);
        padding-bottom: 80px;
        margin: 0 -30px;
    }
    @include media(">=large-desktop") {
        padding-bottom: 135px;
        margin: 0 -74px;
    }
}

.reviews-section-rating {
    padding-bottom: 99px;
    @include media(">=tablet-landscape") {
        padding-bottom: 71px;
    }
    @include media(">=tablet-landscape") {
        width: 65%;
    }
}

.reviews-section-tip {
    padding-bottom: 40px;
    @include media(">=tablet-landscape") {
        padding-bottom: 71px;
    }
    @include media(">=tablet-landscape") {
        width: 35%;
    }
}

.reviews-section-rating,
.reviews-section-tip {
    @include media(">=tablet-landscape") {
        padding: 0 30px;
    }
    @include media(">=large-desktop") {
        padding: 0 74px;
    }
}