.footer {
    padding: 45px 0 10px;
    background-image: $block-gradient;
    @include media(">=tablet") {
        text-align: center;
    }
    @include media(">=desktop") {
        padding: 50px 0 36px;
        .container {
            padding: 0 110px;
            @include flex(row-reverse, space-between, center);
        }
    }
    @include media(">=medium-desktop") {
        padding-bottom: 31px;
        .container {
            padding: 0 87px;
        }
    }
    .copyright {
        margin-bottom: 13px;
        font-size: 14px;
        font-weight: 500;
        letter-spacing: 0.18px;
        @include media(">=tablet") {
            font-size: 16px;
        }
        @include media(">=desktop") {
            font-size: 18px;
        }
    }
}

.footer-nav {
    @include media(">=mobile-landscape") {
        @include flex(row, flex-start, flex-start, wrap);
        margin-left: -15px;
        margin-right: -15px;
    }
    @include media(">=tablet") {
        justify-content: center;
    }
    @include media(">=desktop") {
        margin: 0 -10px;
    }
    @include media(">=medium-desktop") {
        margin: 0 -17px;
    }
    li {
        @extend %reset-list-bullets;
        padding-bottom: 11px;
        font-weight: 700;
        letter-spacing: 0.6px;
        @include media(">=mobile-landscape") {
            padding-left: 15px;
            padding-right: 15px;
        }
        @include media(">=desktop") {
            padding: 0 10px 15px;
            font-size: 14px;
        }
        @include media(">=medium-desktop") {
            font-size: 16px;
            padding-left: 17px;
            padding-right: 17px;
        }
    }
    a {
        color: $white;
        .no-touch &:hover {
            color: $orange;
        }
    }
}