.dual-box {
    margin-bottom: 80px;
    &.decor-box {
        padding-top: 86px;
        margin-bottom: 40px;
        border-radius: 10px;
        background-image: $block-gradient;
    }
    @include media(">=tablet-landscape") {
        margin-bottom: 135px;
        &.decor-box {
            padding-top: 104px;
            margin-bottom: 235px;
        }
    }
    @include media(">=medium-desktop") {
        &.decor-box {
            padding-top: 41px;
            margin-bottom: 103px;
        }
        &.integration-section {
            padding-top: 101px;
        }
    }
}

.dual-box-inner {
    @include media(">=tablet-landscape") {
        @include flex(row, flex-start, center);
        margin: 0 -30px;
        .dual-box.reverse & {
            flex-direction: row-reverse;
        }
    }
    @include media(">=medium-desktop") {
        margin: 0;
        .dual-box.integration-section & {
            align-items: flex-start;
        }
        .dual-box.decor-box & {
            margin: 0 -40px;
        }
        .dual-box.reverse & .dual-box-content {
            margin: 0 auto;
        }
    }
}

.dual-box-descr {
    padding-bottom: 52px;
    @include media(">=tablet-landscape") {
        padding-bottom: 0;
    }
    @include media(">=tablet-landscape", "<medium-desktop") {
        .dual-box.tablet-preview-wide & {
            width: 45%;
        }
        .dual-box.tablet-preview-sm & {
            width: 55%;
        }
    }
    @include media(">=medium-desktop") {
        .dual-box.integration-section & {
            width: 58%;
        }
    }
}

.dual-box-preview {
    @include media(">=tablet-landscape", "<medium-desktop") {
        .dual-box.tablet-preview-wide & {
            width: 55%;
        }
        .dual-box.tablet-preview-sm & {
            width: 45%;
        }
    }
    @include media(">=medium-desktop") {
        .dual-box.integration-section & {
            width: 42%;
        }
    }
}

.dual-box-descr,
.dual-box-preview {
    @include media(">=tablet-landscape") {
        width: 50%;
        padding: 0 30px;
    }
    @include media(">=medium-desktop") {
        padding: 0;
        .dual-box.decor-box & {
            padding: 0 40px;
        }
    }
}

.dual-box-content {
    @include media(">=tablet-landscape") {
        max-width: 510px;
    }
    .dual-box.decor-box & {
        padding: 0 24px;
        @include media(">=tablet-landscape") {
            padding: 0 0 0 59px;
            max-width: none;
        }
        @include media(">=medium-desktop") {
            padding-left: 15%;
        }
    }
    & > *:last-child {
        margin-bottom: 0;
    }
    > strong {
        display: block;
        margin-bottom: 9px;
        color: rgba($light-orange, .9);
        font-size: 12px;
        font-weight: 700;
        text-transform: uppercase;
        letter-spacing: 0.15px;
        @include media(">=tablet") {
            font-size: 14px;
        }
        @include media(">=medium-desktop") {
            .dual-box.decor-box & {
                margin-bottom: 0;
            }
        }
    }
    h2 {
        @extend .h1;
    }
    p {
        margin-bottom: 45px;
        color: $gray;
        font-weight: 500;
        line-height: 1.7;
        letter-spacing: 0.2px;
    }
    .button {
        min-width: 100%;
        @include media(">=tablet") {
            min-width: 231px;
            width: 0;
        }
    }
}

.dual-box-img {
    font-size: 0;
    line-height: 0;
    @include media(">=tablet", "<tablet-landscape") {
        max-width: 60%;
        margin: 0 auto;
    }
    @include media(">=tablet-landscape") {
        .dual-box.integration-section & {
            margin-top: -26%;
            margin-bottom: -26%;
            padding-right: 40px;
        }
    }
    @include media(">=desktop") {
        .dual-box.centered-picture & {
            text-align: center;
        }

    }
}